import {Col, Layout, Nav, Row, Space, Typography, Spin} from '@douyinfe/semi-ui';
import {useEffect, useState} from 'react';
import axios from 'axios'
import ReactMarkdown from 'react-markdown';

function Logo () {
    
    const { Title } = Typography
    
    return (
        <Space vertical style={{height: '100%'}} spacing={2}>
            <Row style={{height: '80%'}}>
                <Col span={24}>
                    <img src={'https://wongjinggitt.github.io/public/logo.svg'} style={{maxWidth: '100%', height: 'auto'}}/>
                </Col>
            </Row>
            <Row style={{height: '20%'}}>
                <Col>
                    <Title ></Title>
                </Col>
            </Row>
        </Space>
    )
}

function DirectoryTree({ data, index }) {
    return (
        <>
            {
                data.type === 'directory' && (
                    <Nav.Sub
                        level={index}
                        itemKey={data.path}
                        text={data.name}
                        type={data.type}
                    >
                        {
                            data?.children?.map( child => {
                                return <DirectoryTree data={child} index={index+1} />
                            } )
                        }
                    </Nav.Sub>
                )
            }
            {
                data.type === 'file' && (
                    <Nav.Item level={index} itemKey={data.path} text={data.name.replace(/.md/g, '')} type={data.type} />
                )
            }
        </>
    );
}

function App() {
    
    const { Content, Sider } = Layout
    const [blogList, setBlogList] = useState([])
    const [selectedKey, setSelectKey] = useState('')
    const [contentLoading, setContentLoading] = useState(false)
    const [openKeys, setOpenKeys] = useState([])
    const [markdown, setMarkdown] = useState('')
    
    useEffect(() => {
        axios.get('https://wongjinggitt.github.io/config/blog_list.json')
            .then(res => {
                setBlogList(res.data?.children)
            })
    }, [])
    
    
    useEffect(() => {
        setContentLoading(true)
        axios.get(`https://wongjinggitt.github.io/${selectedKey}`)
            .then(res => {
                setContentLoading(false)
                setMarkdown(res.data)
            })
    }, [selectedKey])
    
    return (
        <Layout
            style={{height: '100vh'}}
        >
            <Sider
                style={{height: '100%'}}
            >
                <Nav
                    limitIndent={false}
                    style={{ height: '100%' }}
                    bodyStyle={{maxHeight: '100%'}}
                    selectedKeys={[selectedKey]}
                    openKeys={openKeys}
                    onSelect={data => {
                        setSelectKey(data.itemKey)
                    }}
                    onOpenChange={item => setOpenKeys(item.openKeys)}
                >
                    <Nav.Header
                        style={{ padding: '0px 8px 0px 5.5px', height: '30%' }}
                        children={<Logo />}
                    />
                    {
                        blogList?.map( data => <DirectoryTree data={data} index={0} /> )
                    }
                </Nav>
            </Sider>
            <Content>
                <div style={{marginLeft: '20%', marginRight: '20%', height: '100%'}}>
                    <div style={{maxHeight: '100%', overflowY: 'auto'}}>
                        {
                            contentLoading ? (
                                <div
                                    style={{
                                        position: 'absolute',
                                        top: '50%',
                                        left: '50%'
                                    }}
                                >
                                    <Spin/>
                                </div>
                            ) : <ReactMarkdown>{markdown}</ReactMarkdown>
                        }
                    </div>
                </div>
            </Content>
        </Layout>
    )
}

export default App;
